@mixin gridLeft {
    margin-left: 2%;
    margin-right: 0.1%;
    margin-bottom: 1.5%;
}
@mixin gridRight {
    margin-left: 1.5%;
    margin-right: 2%;
    margin-bottom: 1.5%;
}
@mixin background {
    width: 70%;
    //height: 200%;
    height: 600px;
    background-position: 50% 0%;
    background-size: cover;
    background-repeat: no-repeat;

}
@mixin flex {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.about {
    position: absolute;
    top: 600px;
    width: 100%;
    height: 4708px;
    // display: grid;
    // grid-template-columns: 1fr 1fr;
    // grid-template-rows: 1fr 1fr;
    // gap: 0px 0px;
    // grid-template-areas:
    //   "grid1 grid2"
    //   "grid3 grid4";
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.flex1, .flex2 , .flex4{
    width: 100%;
    height: 600px;
    display: flex;
    justify-content: center;
}
.grid1 {
    @include gridLeft;
    @include background;
   
    grid-area: grid1;
    background-image: url('./img/img1.jpg')
}
.grid2 { 
    @include gridRight;
    @include background;
    @include flex;
    background-color: rgba(236, 236, 236, 0.89);
    grid-area: grid2;
}
.grid3 {
    @include gridLeft;
    @include background;
    @include flex;
    background-color: rgba(236, 236, 236, 0.89);
    // margin-top: 49.8%;
    margin-top: 1.5%;
    grid-area: grid3;
}
.grid4 {
    @include gridRight;
    @include background;
    // margin-top: 49.8%;
    margin-top: 1.5%;
    grid-area: grid4;
    background-image: url('./img/img2.jpg');
}
.grid5 {
    @include gridLeft;
    @include background;
    background-image: url('./img/img3.jpg');
}
.grid6 {
    @include gridRight;
    @include background;
    @include flex;
    background-color: rgba(236, 236, 236, 0.89);
}

.text-container {
    @include flex;
    width: 68%;
    height: 90%;
    h1 {
        color: rgb(56, 56, 56);
        font-size: 43px;
        line-height: 38px;
        text-align: center;
    }
    h5 {
        color: rgb(80, 80, 80);
        margin-bottom: 38px;
    }
    p{
        text-align: center;
        color: rgb(77, 77, 77);
        font-size: 14px;
        letter-spacing: -0.5px;
        margin-bottom: 10px;
    }
    button {
        margin-top: 20px;
        background: none;
        width: 160px;
        height: 50px;
        border: 2px black solid;
        transition: all 0.5s;

        a {
            text-decoration: none;
            color: rgb(73, 73, 73);
            font-size: 10px;
            transition: all 0.5s;
        }
    }
    button:hover {
        background: gray;
        border: 2px gray solid;
        a{
            color: white;
        }
    }
}
.flex3 {
    @include flex;
    justify-content: space-around;
    flex-direction: row;
    margin-top: 70px;
    margin-bottom: 70px;
    padding-left: 120px;
    padding-right: 120px;

    div {
        @include flex;
        
        h1 {
            font-size: 38px;
            line-height: 37px;
            color: rgb(36, 36, 36);   
        }
        p {
            font-size: 12px;
            color: rgb(42, 42, 42);
        }
    }
}

.flex5 {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    -webkit-flex-direction: row;
    width: 100%;
    height: 400px;
    
    div {
        @include flex;
        margin-top: 3%;
        margin-right: 0.7%;
        margin-left: 0.7%;
        width: 31.1%;
        height: 100%;
        background-color: rgba(236, 236, 236, 0.89);

        img {
            width: 142px;
            height: 142px;
        }
        h1 {
            color: rgb(56, 56, 56);
            font-size: 50px;
            letter-spacing: -2.5px;
            line-height: 70px;
            text-align: center;
        }
        p{
            width: 80%;
            text-align: center;
            color: rgb(77, 77, 77);
            font-size: 14px;
            letter-spacing: -0.5px;
            margin-bottom: 10px;
        }
    }
}
.flex6 {
    @include flex;
    margin-top: 140px;
    width: 100%;
    height: 300px;
    h1 {
        color: rgb(56, 56, 56);
        font-size: 50px;
        letter-spacing: -2.5px;
        line-height: 30px;
        margin-bottom: 16px;
        text-align: center;
    }
    h5 {
        color: rgb(80, 80, 80);
        margin-bottom: 60px;
    }
    h4 {
        color: rgb(80, 80, 80);
        font-style: italic;
        font-weight: 400;
    }
    h2 {
        color: rgb(80, 80, 80);
        font-size: 19px;
        font-weight: 400;
    }
}
#skrollr-body {
    position: absolute;
    top: 0px;
    width: 100%;
    height: 4708px;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.grid7, .grid8, .grid9 {
    background-color: rgba(236, 236, 236, 0.89);
    transition: all 0.5s;
    width: 100%;
}
.grid7:hover {
    background-color: rgba(223, 200, 165, 0.733);
}
.grid8:hover {
    background-color: rgba(255, 177, 174, 0.548);
}
.grid9:hover {
    background-color: rgba(190, 253, 255, 0.733);
}