@mixin flex {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.form {
    @include flex;
    width: 100%;
    height: 700px;
    .form-intro {
        @include flex;
        margin-top: 4%;
        h1 {
            color: rgb(56, 56, 56);
            font-size: 43px;
            line-height: 38px;
            text-align: center;
        }
        p{
            text-align: center;
            color: rgb(77, 77, 77);
            font-size: 14px;
            letter-spacing: -0.5px;
            margin-bottom: 10px;
        }
    }
    form {
        @include flex;
        margin-top: 10%;
        label {
            margin-top: 3%;
            color: rgb(56, 56, 56);
            font-size: 18px;
            font-weight: 700;
        }
        input {
            margin-top: 1%;
            width: 590px;
            height: 38px;
            padding: 12px 20px;
            border: 1px solid #ccc;
            color: rgb(211, 211, 211);
        }
        button {
            outline: none;
            margin-top: 6%;
            background: none;
            border: 2px black solid;
            width: 120px;
            height: 50px;
            transition: all 0.5s;
            color: rgb(83, 83, 83);
            cursor: pointer;
            text-align: center;
        }
        button:hover {
            border: 2px rgb(173, 173, 173) solid;
            background: rgb(173, 173, 173);
            color: white;
        }
    }
}