@mixin flex {
    display: flex;
    justify-content: center;
    align-items: center;
}
.contact::before {
    content: '';
    width: 96.6%;
    height: 100%;
    position: absolute;
    background: rgb(29, 29, 29); 
    z-index: -1;
    margin-bottom: 3%
}
.contact {
    @include flex;
    position: relative;
    justify-content: space-around;
    width: 100%;
    height: 300px;

    img {
        width: 80px;
        height: 80px;
        margin-bottom: 8%;
    }
    div {
        @include flex;
        flex-direction: column;
        align-items: initial;
        color: white;
        font-size: 13px;
        font-weight: 400;
        margin-bottom: 2%;
        h1 {
            font-size: 16px;
            color: rgb(104, 104, 104);
        }
    }
    .footer-menu {
        h1 {
            margin-bottom: 55%
        }
        div {
            @include flex;
            align-items: initial;
            a {
                text-decoration: none;
                color: white;
                margin-bottom: 13%;
            }
        }
    .footer-address, .footer-contact {
        h3 {
            font-weight: 400;
        }
    }
    }
}
.footer-address {
    h1 {
        margin-top: 10%;
        margin-bottom: 28%;
    }
    p {
        line-height: 24px;
    }
}
.footer-contact {
    h1 {
        margin-top: -27%;
        margin-bottom: 22%;
    }
    p {
        line-height: 24px;
    }
}