@mixin flex {
    display: flex;
    justify-content: center;
    align-items: center;
}
@mixin background {
    background-position: 50% 0%;
    background-size: cover;
    background-repeat: no-repeat;

}

.portfolio {
    @include flex;
    flex-direction: column;
    width: 100%;
    height: 800px;
    margin-top: 6%;
    .port-row-2 {
        margin-top: -7.5%;
    }
    div {
        @include flex;
        width: 100%;
        height: 400px;
        div {
            @include flex;
            width: 23.2%;
            height: 73%;
            margin-left: 0.6%;
            margin-right: 0.6%;
        }
    }

}
.port1 {
    @include flex;
    justify-content: initial;
    flex-direction: column;
    h1 {
        color: rgb(56, 56, 56);
        font-size: 43px;
        line-height: 38px;
        text-align: initial;
    }
    h5 {
        color: rgb(80, 80, 80);
        margin-bottom: 38px;
        text-align: initial;
        margin-top: 3%;
    }
    p{
        text-align: initial;
        color: rgb(77, 77, 77);
        font-size: 14px;
        letter-spacing: -0.5px;
    }
}
.port2, .port3, .port4, .port5, .port6, .port7, .port8 {
    @include background;
    cursor: pointer;
}
.port2::after, .port3::after, .port4::after, .port5::after, .port6::after, .port7::after, .port8::after {
    content: '';
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0);
    transition: all 0.5s;
}

.port2 {background-image: url('./img/img1.jpg');}
.port3 {background-image: url('./img/img2.jpg');}
.port4 {background-image: url('./img/img3.jpg');}
.port5 {background-image: url('./img/img4.jpg');}
.port6 {background-image: url('./img/img5.jpg');}
.port7 {background-image: url('./img/img6.jpg');}
.port8 {background-image: url('./img/img7.jpg');}

.port2:hover, .port3:hover, .port4:hover, .port5:hover, .port6:hover, .port7:hover, .port8:hover  {
    filter: grayscale(1);
}